import React from 'react';
import ReactDOM from 'react-dom'
import {Link, Router} from '@reach/router'
import './App.css';
import Hjem from './komponenter/Hjem'
import Pasta from './komponenter/Pasta'
import Pizza from './komponenter/Pizza'
import Burger from './komponenter/Burger'
import Matretter from './komponenter/Matretter'

const App = () => {
  const pastaretter = Matretter.oppskrifter.filter( matrett => matrett.Kategori === "Pasta")
  const pizzaretter = Matretter.oppskrifter.filter( matrett => matrett.Kategori === "Pizza")
  const burgerretter = Matretter.oppskrifter.filter( matrett => matrett.Kategori === "Burger")
  return(
    <>
      <header>
        <nav className='top-nav'>
          <Link className="feitSkrift" to='/'>Hjem</Link>
          <Link to='pasta'>Pasta</Link>
          <Link to='pizza'>Pizza</Link>
          <Link to='burger'>Burger</Link>
        </nav>
      </header>
      <Router>
        <Hjem path='/' /> 
        <Pasta oppskrifter={pastaretter} path='/pasta' />
        <Pizza oppskrifter={pizzaretter} path='/pizza' />
        <Burger oppskrifter={burgerretter} path ='/burger' />
      </Router>
    </>
  )
}

export default App;
