import React, {useState} from 'react'

const Burger = (props) => {

    
    const [valgt, setValgt] = useState({})

    const [overlaySynlig, setOverlaySynlig] = useState(false)

    const endreSynlig = () => {
        setOverlaySynlig(!overlaySynlig)
    }

    const fjern = () => {
        setOverlaySynlig(false)
    }

    const lagTekst = (oppskrift) => {
        endreSynlig();
        setValgt(oppskrift)
    }


    return (
        <section onclick={ fjern } className="oppskrifter">
            <h2 className="overskriftP">Mine favoritt burger oppskrifter</h2>
            {
                props.oppskrifter.map(oppskrift =>
                    <div onClick={ () => lagTekst(oppskrift) }>
                        <img src={oppskrift.Image} alt="{oppskrift.Navn}" />
                        <h3>{oppskrift.Navn}</h3>
                        <p>{oppskrift.Beskrivelse}</p>
                    </div>
                    )
            }

            <div className="overlay" style={{ display: overlaySynlig ? "block" : "none" }}>
                <p className="xknap" onClick={fjern}>X</p>
                <h1>{valgt.Navn}</h1>
                <img src={valgt.Image} alt={valgt.Navn} />
                <p className="hvitbak">Ingredienser:</p>
                <ul className="hvitbak">{valgt.Inneholder}</ul>
                <p className="hvitbak">{valgt.Fremgangmate}</p>
            </div>
        </section>
    )
}

export default Burger